import { Facebook, GitHub, Instagram, MailOutline, Phone, Room, Twitter } from '@material-ui/icons';
import React from 'react'
import styled from 'styled-components'
import {mobile} from "../responsive";

const Container= styled.div`
    display:flex;
    ${mobile({flexDirection: "column"})}
`;

const Left = styled.div`
    flex:1;
    display:flex;
    flex-direction:column;
    padding:20px;
`;

const Logo = styled.h1`   
text-align:center;

`;
const Desc = styled.p`   
    margin: 20px 0px;
`;
const SocialContainer = styled.div` 
    display: flex;
    justify-content:space-between;
`;

const SocialIcon = styled.div`
    width:40px;
    height:40px;
    border-radius:50%;
    color: white;
    background-color: #${(props) =>props.color};
    display:flex;
    align-items:center;
    justify-content:center;
    margin-right:20px;
    cursor: pointer;
`;

const Center = styled.div`
  flex:1;
  padding:20px;
  text-align:center;
  ${mobile({ display:"none" })}
`;

const Title= styled.h3`
    margin-bottom:30px;
`;

const List= styled.ul`
    margin:0;
    padding:0;
    list-style:none;
    display:flex;
    flex-wrap: wrap;
    cursor: pointer;

    
`;

const ListItem=styled.li`
    width: 50%;
    margin-bottom:10px;
`;


const Right = styled.div`
  flex:1;
  padding:20px;
  ${mobile({ backgroundColor: "#fff8f8"})}
`;

const ContactItem = styled.div`
    margin-bottom: 20px;
    display:flex;
    align-items:center;
`;

const Payment = styled.img`
    width:50%;
`;
const Footer = () => {
  return (
    <Container>
        <Left>
        <Logo>Georgia Goose Online Store</Logo>
        <Desc>
         This site is made with REACT, NodeJs,Express, and MongoDB as well as countless hours of work just for you!
        </Desc>
        <SocialContainer>
                <SocialIcon color='3b5999'>
                    <Facebook/>
                </SocialIcon>

                <SocialIcon color='E4405F'>
                    <Instagram/>
                </SocialIcon>

                <SocialIcon color='55A4EE'>
                    <Twitter/>
                </SocialIcon>

                <SocialIcon color='E60023'>
                    <GitHub/>
                </SocialIcon>
        </SocialContainer>
        </Left>

        <Center>
            <Title> Helpful Links</Title>
            <List>
                <ListItem>Home</ListItem>
                <ListItem>Cart</ListItem>
                <ListItem>Mens</ListItem>
                <ListItem>Women's</ListItem>
                <ListItem>Accessories</ListItem>
                <ListItem>My Account</ListItem>
                <ListItem>Order Tracking</ListItem>
                <ListItem>Wishlist</ListItem>
                <ListItem>Wishlist</ListItem>
                <ListItem>Terms</ListItem>
            </List>

        </Center>
        <Right>
        <Title>Contact info</Title>
        <ContactItem>
            <Room stye = {{marginRight:"10px"}}/>
           Covington,Georgia
        </ContactItem>
        <ContactItem>
            <Phone style = {{marginRight:"10px"}}/>
           904-210-8619
        </ContactItem>
        <ContactItem>
            <MailOutline style = {{marginRight:"10px"}}/>
            jgossett73@gmail.com
        </ContactItem>
        <Payment src = "https://i.ibb.co/Qfvn4z6/payment.png"/>
        </Right>
    </Container>
  )
}

export default Footer